@keyframes slide-in{
    from {
        opacity: 0;
        transform: translateX(-5rem);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}
  
  
@keyframes bounce {
    0% { 
        transform: translateY(0);
    }
    50% { 
        transform: translateY(-0.3rem);
    }
    100% { 
        transform: translateY(0);
    }
}
